*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: montserrat, sans-serif;  
}

input, button{
  appearance: none;
  background: none;
  border: none;
  outline: none;
}

.App{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #53565A;
}

form{
  display: block;
  position:relative;
}

form:after{
  content: '';
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  z-index: 1;
  background-image: linear-gradient(to bottom right, #ffce00, #fe4880);
}


.form-inner{
  position: relative;
  display:block;
  background-color: aliceblue;
  padding:30px;
  z-index: 2;
}

.form-inner h2{
  color:#888;
  font-size:28px;
  font-weight: 500;
  margin-bottom:30px;
}

form .form-inner .form-group{
  display: block;
  width: 300px;
  margin-bottom: 15px;
}

.form-inner  .form-group label{
  display: block;
  color: #666;
  font-size: 12px;
  margin-bottom:5px;
  transition: 0.4s;
}

.form-inner .form-group:focus-within label{
  color: #fe4880;
}

form .form-inner .form-group input{
  display: block;
  width: 100%;
  padding:10px 15px;
  background-color: aliceblue;
  border-radius: 8px;
  transition: 0.4s;
}

form .form-inner .form-group input:focus{
  box-shadow: 0px 0px 2px rgba(0,0,0,0.2);
}

form .form-inner input[type="submit"], .welcome button{
  display: inline-block;
  padding:10px 15px;
  border-radius: 8px;
  background-image: linear-gradient(to right, #FFCe00 50%,#fe4880 );
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: #FFF;
  font-weight:700;
  cursor: pointer;
}

form .form-inner input[type="submit"]:hover, .welcome button{
    background-position: 100% 0%;
}

.welcome{
  width:100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  background-color: #FFF;
  padding:15px 30px;
}

.welcome h2{
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom:30px;
}
